*{
  box-sizing: border-box;
}
body {
  margin: 0;
  padding: 0;
  /*background-color: #030404;*/
  background-color: #FFF;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  background: #030404;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-float infinite 3s ease-in-out;
  }
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.App-link {
  color: rgb(112, 76, 182);
}

@keyframes App-logo-float {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(10px)
  }
  100% {
    transform: translateY(0px)
  }
}

header .navigation {
  background-color: black;

  position: fixed;
  color: #ffffff;
  top: 0;
  height: 70px;
  width: 100%;
  opacity: 0.9;
  z-index: 150;
}
.Logo {
  padding: 2px;
  float: left;
  text-decoration: none;
}
.Logo img {
  padding-left: 10px;
  padding-bottom: 25px;
  height: 90px;
  text-decoration: none;
  color: #ffffff;
  cursor: pointer;
}

.btn-sign-in a {
  background-color: #13ffff;
  background-image: linear-gradient(to bottom right, #13ffff, #3ba3ff, #1a2980);
  color: white;
  margin-right: 36px;
  align-content: center;
  padding: 14px 25px;
  margin-top: 10px;
  text-align: center;
  text-decoration: none;
  float: right;
  border-radius: 25px;
}

.btn-sign-in a:hover,
a:active {
  background-color: transparent;
  color: red;
  border-style: solid;
  border-color: red;
  border-width: thin;
}

@media screen and (max-width: 960px) {
  header .navigation {
    width: 100%;
    height: 70px;
  }
  .Logo img {
    position: absolute;
    top: 0;
    left: 0;
    width: 200px;
    height: auto;
  }
  .btn-sign-in a {
    padding-top: 5px;
    margin-right: 10px;
    text-align: center;
    align-content: center;
  }
}

.banner-heading {
