  border-radius: 30px;
  color: #767676;
  background-color: #e3e3e3;
  line-height: 40px;
  font-weight: 600;
  text-align: center;
  transform: translateX(-50%);
  font-size: 14px;
  text-transform: uppercase;
}
.works-container .main-line-step2-btn{
  position: absolute;
  content: "";
  top: 1970px;
  left: 50%;
  width: 110px;
  height: 40px;
  border-radius: 30px;
  color: #2a7bc2;
  background-color: #c1ddf5;
  line-height: 40px;
  font-weight: 600;
  text-align: center;
  transform: translateX(-50%);
  font-size: 14px;
  text-transform: uppercase;
}
.works-container .main-line-step3-btn{
  position: absolute;
  content: "";
  top: 2450px;
  left: 50%;
  width: 110px;
  height: 40px;
  border-radius: 30px;
  color: #eb305f;
  background-color: #f9c8d4;
  line-height: 40px;
  font-weight: 600;
  text-align: center;
  transform: translateX(-50%);
  font-size: 14px;
  text-transform: uppercase;
}
.works-container :last-child::before{
  content: "\f00c";
  font-family: fontawesome;
  position: absolute;
  line-height: 60px;
  font-size: 22px;
  color: #00b489;
  z-index: 99;
  transform: translateX(-50%);
  bottom: 0;
  left: 50%;
  top: 2980px;
}
.works-container :last-child::after{
  content: '';
  position: absolute;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  background: #a9e6cf;
  transform: translateX(-50%);
  animation-duration: 5s;
  animation-timing-function: ease;
  animation-delay: 0s;
  animation-iteration-count: infinite;
  animation-direction: normal;
  animation-fill-mode: none;
  animation-play-state: running;
  animation-name: timeline_success_boxshadow;
  bottom: 0;
  left: 50%;
  top: 2980px;
}
